(function() {
  angular.module('notely')
    .config(usersConfig);

  usersConfig.$inject = ['$stateProvider'];
  function usersConfig($stateProvider) {
    $stateProvider
      .state('sign-up', {
        url: '/sign-up',
        // Use a directive we have defined ourselves.
        template: '<sign-up></sign-up>'
      });
  };
})();
